body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  font-size: 22px;
  font-weight: 700;
}

.calc-body {
  width: 250px;
  background: lightslategrey;
  border-radius: 20px;
  margin: 10px;
  padding: 30px 20px;
}

.screen-container {
  padding-bottom: 50px;
}

.screen-container input {
  display: block;
  width: 85%;
  border: none;
  height: 50px;
  text-align: right;
  padding: 0 10px;
  margin: 0 auto;
  color: lightslategrey;
  border-radius: 10px;
}

.buttons-container {
  overflow: hidden;
  width: 100%;
  margin: 0 auto;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  padding-bottom: 20px;
}

.buttons-container button {
  width: 20%;
  height: 40px;
  line-height: 30px;
  margin: 2%;
  border: none;
  outline: none;
  background: white;
  color: lightslategrey;
  border-radius: 10px;
  cursor: pointer;
}
